/* SignUpForm.css */

.signup-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;
    margin-top: 40px;
  }
  
  .signup-card {
    padding: 30px 10px;
    border-radius: 8px;
    width: 400px;
    text-align: center;
    margin-top: 32%;
  }
  
  .signup-form {
    border: 2px solid #ddd;
    border-radius: 8px;
    padding: 20px;
    margin-top: 10px;
  }
  
  .signup-form h1 {
    margin-bottom: 20px;
  }
  
  .signup-form input,
  .signup-form button {
    margin-bottom: 10px;
  }
  
  .signup-form input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .signup-form button {
    width: 100%;
    padding: 10px;
    background-color: #357717;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .signup-form button:hover {
    background-color: #357717;
  }
  
  .signup-form p {
    margin-top: 10px;
    margin-bottom: 0;
    color: black;
  }
  
  .login-link4 {
    color: #357717;
    text-decoration: none;
    
  }
  
  .login-link:hover {
    text-decoration: underline;
    color: #357717;
  }
  
  .form-group {
    margin-bottom: 1rem;
    display: flex;
    flex-direction: column;
  }
  
  .form-label {
    margin-bottom: 0.25rem;
    font-weight: 500;
    color: #333;
  }
  
  .form-select {
    padding: 0.5rem 0.75rem;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 1rem;
    background-color: white;
    color: #333;
    transition: border-color 0.3s ease;
  }
  
  .form-select:focus {
    outline: none;
    border-color: #4A90E2;
    box-shadow: 0 0 0 2px rgba(74, 144, 226, 0.2);
  }
  
  .form-select:hover {
    border-color: #888;
    cursor: pointer;
  }
  
  /* Role Selection Container */
.signup-form .role-selection {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

/* Style each label like a button */
.signup-form .role-selection label {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  border: 2px solid #ddd;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
}

/* Highlight selected option */
.signup-form .role-selection input[type="radio"]:checked + span {
  border-color: #357717;
  color: #357717;
}

/* Hide raw radio circle */
.signup-form .role-selection input[type="radio"] {
  display: none;
}

/* Handle hover state */
.signup-form .role-selection label:hover {
  border-color: #357717;
}
